import React from "react";

import { Grid, Typography } from "@material-ui/core";

const Cloudiness = ({ singleDay }) => {
  return (
    <>
      <Grid item xs={6} container direction="row" justify="flex-start" alignItems="center">
        <Grid style={{ marginRight: 12 }}>
          <svg
            xmlns="http://www.w3.org/2000/svg"
            width="16"
            height="16"
            fill="currentColor"
            className="bi bi-cloud-fill"
            viewBox="0 0 16 16"
          >
            <path d="M4.406 3.342A5.53 5.53 0 0 1 8 2c2.69 0 4.923 2 5.166 4.579C14.758 6.804 16 8.137 16 9.773 16 11.569 14.502 13 12.687 13H3.781C1.708 13 0 11.366 0 9.318c0-1.763 1.266-3.223 2.942-3.593.143-.863.698-1.723 1.464-2.383z" />
          </svg>
        </Grid>
        <Grid>
          <Typography>Cloudiness</Typography>
          <Typography>{singleDay.forecastCloudiness}%</Typography>
        </Grid>
      </Grid>
    </>
  );
};

export default Cloudiness;
